html {
  height: 100%;
}

body, #root {
  min-height: 100vh;
}

body.dark {
  color: white;
}

* {
  line-height: 1.6rem;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.logo img {
  height: 60px;
  width: auto;
}

header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px;
}

footer a, footer p {
  text-decoration: none;
  padding: 10px;
  color: black;
  font-weight: 600;
  font-size: 1.1rem;
  margin: 0px;
  cursor: pointer;
}

footer a:hover, footer p:hover {
  background-color: black;
  color: white;
}

ul {
  margin: 0px;
  padding: 0px;
}

ul li {
  list-style: none;
  cursor: pointer;
  box-sizing: border-box;
  border-bottom: transparent solid 3px;
}

ul li:hover {
  border-bottom: solid 3px black;
}

ul li h3 {
  margin: 10px 0px;
}

.content-wrapper {
  display: grid;
  grid-template-columns: 40% 40%;
}

.left-column {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 10px;
  padding: 10px 60px;
  border-right: solid 2px black;
}

.button-container {
  display: flex;
  flex-direction: column;
}

.right-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 10px 40px;
  padding: 10px;
}

h3 {
  margin: 40px 0px;
}

h3.placeholder-text {
  color: grey;
  font-weight: 500;
  font-style: italic;
  margin: 50px 0px;
}

button {
  background-color: transparent;
  border: solid 3px transparent;
  color: black;
  padding: 10px;
  margin: 15px 0px;
  width: fit-content;
  cursor: pointer;
  font-weight: 600;
  font-size: 1.1rem;
}

button.generate-facts {
  width: 250px;
  border: solid 3px black;
  margin: 0px 0px 20px;
}

button:hover {
  background-color: transparent;
  border: solid 3px black;
}

button.active, button.generate-facts:hover {
  background-color: black;
  color: white;
}

.about.visible {
  display: flex;
  position: absolute;
  margin-top: 200px;
  flex-direction: column;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  width: 750px;
  height: 500px;
  align-self: center;
}

.about-text-container {
  display: flex;
  flex-direction: column;
  margin: 0px 50px;
}

.about {
  display: none;
}

.top {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 100%;
  box-sizing: border-box;
}

.top div {
  background-color: transparent;
  padding: 10px 14px;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 600;
}

.top div:hover {
  background-color: black;
  color: white;
}

@media screen and (max-width: 1000px) {

  .content-wrapper {
    display: grid;
    grid-template-columns: unset;
    grid-template-rows: unset;
    justify-content: center;
    margin: 10px;
    box-sizing: border-box;
  }

  .left-column {
    border-right: none;
    border-bottom: solid 3px black;
    padding: 30px 0px;
    margin: 0px;
    align-items: flex-start;
  }
  
  .button-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .right-column {
    margin: 0px;
    padding: 30px 0px;
  }

  button {
    padding: 3px;
    margin: 10px 0px;
    font-size: 0.8rem;
  }

  .about.visible {
    width: 90%;
  }
}

@media screen and (max-width: 500px) {

  .logo img {
    height: 40px;
  }

  ul li h3 {
    font-size: 1rem;
  }

  header {
    padding: 10px;
  }

  button.generate-facts {
    padding: 10px;
    width: fit-content;
    font-size: 1rem;
  }

  .content-wrapper {
    grid-template-rows: unset;
    margin: 0px 20px;
  }

  .left-column, .right-column {
    padding: 20px 0px;
    height: fit-content;
  }

  .left-column .button-container {
    display: flex;
    flex-direction: row;
  }

  .about.visible {
    width: 90%;
    height: fit-content;
    margin: 10vh;
  }
}

/* Dark Theme */

body.dark {
  background-color: #101010;
  color: white;
}

body.dark .logo img {
  filter: invert(100%);
}

body.dark footer a, body.dark footer p {
  color: white;
}

body.dark footer a:hover, body.dark footer p:hover {
  background-color: white;
  color: #101010;
}

body.dark .left-column {
  border-right: solid 3px white;
}

body.dark button {
  color: white;
}

body.dark button:hover {
  border: solid 3px white;
}

body.dark .option.active {
  background-color: white;
  color: #101010;
}

body.dark button.generate-facts {
  border: solid 3px white;
}

body.dark button.generate-facts:hover {
  background-color: white;
  border: solid 3px white;
  color: #101010;
}

body.dark ul li:hover {
  border-bottom: solid 3px white;
}

body.dark .about {
  background-color: #1a1a1a;
  box-shadow: rgba(0, 0, 0, 0.8) 0px 7px 29px 0px;
}

body.dark .top div:hover {
  background-color: white;
  color: #101010;
}